import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-calculator-input',
  templateUrl: './calculator-input.component.html',
  styleUrls: ['./calculator-input.component.scss']
})
export class CalculatorInputComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {
  }

  months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','August'];
  selectedItem : string = this.months[0];
  toDate : Date = new Date();
  fromDate : Date  = new Date();

  dropdownChanged(val) : void {
    this.selectedItem = val;
    console.log(this.selectedItem);
  }
  toDatepickerChanged(newDate) : void {
    this.toDate = new Date(newDate);
  }
  fromDatepickerChanged(newDate) : void {
    this.fromDate = new Date(newDate);
  }

}
